body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;

}

.container { 
  max-width: 300px;
  margin: 0 auto;
  text-align: center;
}

.blue-color { color: rgb(118, 118, 184);}

.header { display: flex; justify-content: space-between; margin-top: 30px; align-items: center;}
.btn { color: #ffffff; background-color: #000000; padding: 10px 20px; font-size: 20px; border: none; cursor: pointer;}
.task { margin-bottom: 10px; border: 1px solid rgb(43, 133, 133); text-align: left; padding: 5px; background-color: lightgray;}
.close { font-size: 22px; float: right; color: red; cursor: pointer; padding: 10px; background-color: rgb(218, 218, 210); font-weight: 900;}
.reminder { border-left: 2px solid red;}
.add-form { text-align: left; margin: 30px 0;}
label { color: #000; font-weight: 700; font-size: 18px;  }
input[type="text"] { border: 1px solid black; width: 100%; padding: 10px;}
.form-control { margin: 15px 0;}
.save-btn { cursor: pointer; padding: 8px 20px 8px 20px !important; letter-spacing: 1px !important; font-size: 18px; text-transform: uppercase; color: #ffffff; display: inline-block; background-color: #558149;  border-width: 0px !important; border-radius: 0px; width: 100%; }
.Reminder-label { padding-right: 10px;}